/* global window */
import { formatByGroups } from 'lib-bb-iban';
import countries from './constants';

const NO_ITEMS = Symbol('NO_ITEMS');
const NO_ITEMS_CREATE = Symbol('NO_ITEMS_CREATE');
const SEARCH = Symbol('SEARCH');

const EmptyStateConfig = {
  [NO_ITEMS]: {
    iconClassName: 'far fa-address-book',
    title: 'contacts.empty.title',
    description: 'contacts.empty.message.noContact',
  },
  [NO_ITEMS_CREATE]: {
    iconClassName: 'far fa-address-book',
    title: 'contacts.empty.title',
    description: 'contacts.empty.message.create.noContact',
  },
  [SEARCH]: {
    iconClassName: 'fa fa-search',
    title: 'contacts.loadMore.emptySearch',
    description: 'contacts.loadMore.emptySearchMessage',
  },
};

const isCreating = $ctrl => $ctrl.state.page === 'new';

const getEmptyStateConfig = $ctrl => {
  let emptyState;
  if ($ctrl.state.searching) {
    emptyState = SEARCH;
  } else {
    emptyState = isCreating($ctrl) ? NO_ITEMS_CREATE : NO_ITEMS;
  }
  return EmptyStateConfig[emptyState];
};

const resetFormState = form => {
  if (form) {
    form.$setUntouched();
    form.$setPristine();
  }
};

const isFormPristine = form => !form || form.$pristine;

const cancelEditForm = (ext, $ctrl) => {
  if (ext.contactForm) {
    resetFormState(ext.contactForm);
    Object.assign(ext, { contactForm: null });
  }

  if (ext.newContact) {
    Object.assign(ext, { newContact: null });
    $ctrl.showNewContactForm();
  } else if (ext.selectedContact) {
    $ctrl.selectContact(ext.selectedContact);
    Object.assign(ext, { selectedContact: null });
  } else {
    $ctrl.cancelContactForm();
  }
};

const showNewContactForm = (ext, $ctrl) => {
  if (isFormPristine(ext.contactForm)) {
    $ctrl.showNewContactForm();
  } else {
    Object.assign(ext, { cancelFormConfirmOpened: true, newContact: true });
  }
};

const tryToCancelEditForm = (ext, $ctrl) => {
  if (isFormPristine(ext.contactForm)) {
    cancelEditForm(ext, $ctrl);
  } else {
    Object.assign(ext, { cancelFormConfirmOpened: true });
  }
};

const capitalize = (str) => str.substr(0, 1).toUpperCase() + str.substr(1);

const getAddressProperties = prefix =>
  ['addressLine1', 'addressLine2', 'streetName', 'postCode', 'town', 'countrySubDivision']
  .map(prop => (prefix ? prefix + capitalize(prop) : prop));

const getAllProperties = (prefix, props) =>
  (props.includes('address') ? props.concat(getAddressProperties(prefix)) : props);

const notEmptyFilter = value => !!value;

/**
 * @name scrollToFirstError
 * @description Focus or scroll to first error in ```form``` depending if it is visible or not.
 * @inner
 * @type {Function}
 * @param {Object} form Angular form object to check errors
 * @returns {Object} First angular element that is invalid
 */
const scrollToFirstError = (form) => {
  const firstError = form.$$controls.find((control) => control.$invalid).$$element;
  const isVisible = !firstError.hasClass('ng-hide');

  if (isVisible) {
    firstError[0].focus();
  } else {
    window.scrollTo({
      top: firstError[0].parentNode.offsetTop,
      behavior: 'smooth',
    });
  }

  return firstError;
};

/**
 * @description
 * Helpers for ext-bus-contact-list-ng
 *
 * @name Helpers
 * @type {Object}
 */
const helpers = ({ $filter }) => {
  const i18n = $filter('i18n');
  const country = $filter('country');
  let extensionReference;
  let controllerReference;

  return {
    cancelEditForm,
    tryToCancelEditForm,
    showNewContactForm,

    saveContact: ($ctrl, form) => {
      if (form.$valid) {
        const contact = $ctrl.state.contact.data;
        return $ctrl.saveContact(contact).then(() => resetFormState(form));
      }

      scrollToFirstError(form);
      return Promise.reject();
    },

    searchContact: ($ctrl) => {
      const queryString = $ctrl.state.contactsSearch.query;
      if (queryString) {
        $ctrl.search(queryString, undefined, { from: 0 });
      } else {
        $ctrl.cancelSearch();
      }
    },

    getListData: ($ctrl) => (
      $ctrl.state.searching && $ctrl.state.contactsSearch.data ?
        $ctrl.state.contactsSearch.data : $ctrl.state.contacts.data
    ),

    showContactBlock: ($ctrl, contactProperties = []) =>
      getAllProperties('', contactProperties)
        .some((prop) => $ctrl.state.contact.data && !!$ctrl.state.contact.data[prop]),

    showBankBlock: (account, accountProperties = []) =>
      getAllProperties('bank', accountProperties).some((prop) => !!account[prop]),

    showHolderBlock: (account, accountProperties = []) =>
      getAllProperties('accountHolder', accountProperties).some((prop) => !!account[prop]),

    getContactAddress: (dataObj) => getAddressProperties('')
      .map((prop) => dataObj[prop])
      .filter((address) => !!address)
      .join(', '),

    getBankAddress: (account) => getAddressProperties('bank')
      .map((prop) => account[prop])
      .concat(country(account.bankCountry))
      .filter(notEmptyFilter)
      .join(', '),

    getHolderAddress: (account) => getAddressProperties('accountHolder')
      .map((prop) => account[prop])
      .concat(country(account.accountHolderCountry))
      .filter(notEmptyFilter)
      .join(', '),

    getAccountBank: (account) => {
      const bankCode = [account.BIC, account.bankCode]
        .filter(notEmptyFilter)
        .join(' - ');

      return [
        account.bankName,
        bankCode ? `(${bankCode})` : null,
      ]
        .filter(notEmptyFilter)
        .join(' ');
    },

    /**
     * @description
     * Checkes if actual pagination type matches the one, defined in properties
     *
     * @public
     * @name Helpers#isPaginationTypeMatch
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @param {string} type Description of pagination type (pagination, load-more)
     *
     * @returns {boolean}
     */
    isPaginationTypeMatch: ($ctrl, type) => $ctrl.state.pageParams.paginationType === type,

    /**
     * @name Helpers#isSearching
     * @description
     * Check if the search action is in progress
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @return {boolean} Flag indicating search status
     */
    isSearching: $ctrl => $ctrl.state.contactsSearch.loading,

    /**
     * @name Helpers#isLoading
     * @description
     * Check if the loading action is in progress
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @return {boolean} Flag indicating loading status
     */
    isLoading: $ctrl => $ctrl.state.contact.updating || $ctrl.state.contacts.loading,

    /**
     * @name Helpers#getSupportedCountries
     * @type {function}
     *
     * @description
     * Get listed of countries
     *
     * @return {Array} countries List of countries
     */
    getSupportedCountries: () => countries,

    /**
     * @name Helpers#openAccountForm
     * @type {function}
     *
     * @description
     * Add new or edit existing account
     *
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @param ext
     * @param accountIndex
     */
    openAccountForm: ($ctrl, ext, accountIndex) => {
      extensionReference = ext;
      controllerReference = $ctrl;
      extensionReference.$ctrl = $ctrl;
      extensionReference.account = $ctrl.getAccount(accountIndex) || {};
      extensionReference.accountIndex = accountIndex;
      extensionReference.open = true;
    },

    /**
     * @name Helpers#submitAccountForm
     * @type {function}
     *
     * @description
     * Save or update existing account
     *
     * @param {object} account - account data
     * @param {number} accountIndex - account index
     * @param {object} form - account form
     */
    submitAccountForm: (account, accountIndex, form) => {
      if (form.$invalid) {
        scrollToFirstError(form);
        return;
      }

      if (extensionReference) {
        extensionReference.open = false;
      }

      Object.keys(account).forEach(fieldName => {
        if (account[fieldName] === '') {
          Object.assign(account, { [fieldName]: null });
        }
      });

      if (controllerReference) {
        if (controllerReference.getAccount(accountIndex)) {
          controllerReference.updateAccount(account, accountIndex);
        } else {
          controllerReference.addAccount(account);
        }
      }
    },

    /**
     * @name Helpers#deleteContactAccount
     * @type {function}
     *
     * @description
     * Delete selected account by its index
     *
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @param {number} accountIndex index of account to delete
     */
    deleteContactAccount: ($ctrl, accountIndex) => {
      $ctrl.deleteContactAccount(accountIndex);
    },

    /**
     * @name Helpers#dismissAccount
     * @type {function}
     *
     * @description
     * Dismiss creating new account
     */
    dismissAccount: () => {
      if (extensionReference) {
        extensionReference.open = false;
      }
    },

    /**
     * @description
     * Check if create contact form is being used
     *
     * @name Helpers#isCreating
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @returns {boolean}
     */
    isCreating,

    /**
     * @description
     * Returns the empty state icon classes
     *
     * @name Helpers#getEmptyIconClasses
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @returns {string}
     */
    getEmptyIconClasses: $ctrl =>
      `${getEmptyStateConfig($ctrl).iconClassName} fa-4x text-muted`,

    /**
     * @description
     * Returns the empty state translated title
     *
     * @name Helpers#getEmptyTitle
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @returns {string}
     */
    getEmptyTitle: $ctrl => i18n(getEmptyStateConfig($ctrl).title),

    /**
     * @description
     * Returns the empty state translated description
     *
     * @name Helpers#getEmptyDescription
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @returns {string}
     */
    getEmptyDescription: $ctrl => i18n(getEmptyStateConfig($ctrl).description),

    /**
     * @description
     * Checks if there is any account loaded in the list, regardless if it is searching or not
     *
     * @name Helpers#isContactListEmpty
     * @type {function}
     * @param {Object.<module:widget-bb-contact-ng.ContactController>} $ctrl Current controller
     * @returns {boolean}
     */
    isContactListEmpty: $ctrl => {
      if ($ctrl.state.searching) {
        return !$ctrl.state.contactsSearch.loading && !$ctrl.hasSearchContacts();
      }

      return !$ctrl.state.contacts.loading && !$ctrl.hasContacts();
    },

    showAccountDeleteConfirmation: (ext, index, account) => Object.assign(ext, {
      accIndexToDelete: index,
      accNameToDelete: account.alias || account.name || '',
      deleteAccConfirmOpened: true,
    }),

    /**
     * @name Helpers#formatIBAN
     * @type {function}
     *
     * @description
     * Convert an IBAN to human format adding a space every 4 characters.
     *
     * @param {string} IBAN IBAN account number
     *
     * @returns {string} Human readable IBAN account number
     */
    formatIBAN: formatByGroups,

    /**
     * @name Helpers#isInvalid
     * @type {Function}
     *
     * @description
     * Checks control with ```controlName``` validity within the ```form```
     *
     * @param {Object} form Angular form where control is placed in
     * @param {String} controlName  Name that identifies control in ```form```
     *
     * @returns {Boolean} Flag indicating control validity
     */
    isInvalid: (form, controlName) => form.$submitted && form[controlName].$invalid,

  };
};

export default helpers;



